import React from 'react';
import { withRouter, Switch, Redirect,Route } from 'react-router-dom'
import LoadableComponent from '../../../utils/LoadableComponent'

import { Breadcrumb } from 'antd';
import './index.css'
// import { p } from 'gsap/RoundPropsPlugin';
const HomePage = LoadableComponent(()=>import('../HomePage/index'))
const Column = LoadableComponent(()=>import('../Column/index'))
const All = LoadableComponent(()=>import('../All/index'))
const Banner = LoadableComponent(()=>import('../Banner/index'))
const ExamineColumn = LoadableComponent(()=>import('../ExamineColumn/index'))
const ExamineDept = LoadableComponent(()=>import('../ExamineDept/index'))


class Content extends React.Component {
    constructor(props){
        super(props)

    }
    render() {
        return (
            <div id="contentArticle">
               <Switch>
                    <Route   path='/Article/Index/HomePage' component={HomePage}></Route>
                    <Route exact path='/Article/Index/Column' component={Column}></Route>
                    <Route exact path='/Article/Index/ExamineDept' component={ExamineDept}></Route>
                    <Route exact path='/Article/Index/ExamineColumn' component={ExamineColumn}></Route>

                    <Route exact path='/Article/Index/Banner' component={Banner}></Route>

                    <Route exact path='/Article/Index' component={All}></Route>
                </Switch>
            </div>
        )
    }
}
export default Content