import React from "react";
import NProgress from 'nprogress'

import { Card, Collapse, BackTop, Form, Input, message, Icon } from 'antd'
import { EditorState, convertToRaw, ContentState } from 'draft-js';
import api from "../../../api/api";
import { isAuthenticated } from "../../../utils/Session";

import { Editor } from 'react-draft-wysiwyg';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import 'nprogress/nprogress.css'
import './index.css'
const content = { "entityMap": {}, "blocks": [{ "key": "637gr", "text": "Initialized from content state.", "type": "unstyled", "depth": 0, "inlineStyleRanges": [], "entityRanges": [], "data": {} }] };
const { TextArea } = Input;
const Panel = Collapse.Panel;
const getSvgIcon = (style = {}, props, style1) => {

    const path = 'M715.8 493.5L335 165.1c-14.2-12.2-35-1.2-35 18.5v656.8c0 19.7 20.8 30.7 35 18.5l380.8-328.4c10.9-9.4 10.9-27.6 0-37z';
    return (
        <span style={style}>
            <i >
                <svg
                    viewBox="0 0 1024 1024"
                    width="1em"
                    height="1em"
                    fill="currentColor"
                    style={{ verticalAlign: '-.125em' }}
                >
                    <path d={path} />
                </svg>
            </i>

        </span>
    );
};

function expandicon(props) {
    return getSvgIcon({
        position: 'absolute',
        right: 'auto',
        left: '0px',
        color: '#B4B4B4',
        transform: `rotate(${props.isActive ? 90 : 0}deg)`,
        top: 0
    }, props);
}
class Index extends React.Component {
    constructor(props) {
        super(props)
        // console.log(this.props)
    }
    state = {
        editorState: EditorState.createEmpty(),
        contentState: '',
        value: '',
        backUserId: 1,
        deptId: 1,
        listCategory: [],
        secondId: 0,
        firstId: 0,
        selectId:0,
        imgUrl: [],
        shieldList: []
    }
    componentWillMount() {
        let userInfo = JSON.parse(isAuthenticated().split('?')[0])
        this.setState({
            backUserId: userInfo.backUserId, deptId: userInfo.deptId,
        })
        if (this.props.location.params && this.props.location.params.id) {
            api('getArticleInfo', 'post', {}, {
                'articleId': this.props.location.params.id
            }).then(res => {
                if (!res.data.errorMessage) {
                    console.log(res.data.data)
                    const contentState = ContentState.createFromBlockArray(htmlToDraft(res.data.data.content));
                    // console.log(contentState)
                    const editorState = EditorState.createWithContent(contentState);
                    this.setState({
                        editorState,
                        secondId: res.data.data.secondCateId ? res.data.data.secondCateId : 0,
                        firstId: res.data.data.firstCateId,
                        selectId:res.data.data.firstCateId,
                        value: res.data.data.title
                    })
                    this.props.form.setFieldsValue({
                        name: res.data.data.title,
                    });

                } else {
                    message.warning(res.data.errorMessage, 2, () => {
                        this.props.history.push('/Article/Index')
                        // this.props.history.go(-1)
                    })

                }
            })
        }
        api('listCategory', 'post', {}, { 'type': 1, status: 1 }).then(res => {
            if (!res.data.errorMessage) {
                let array = res.data.data
                for (let index = 0; index < array.length; index++) {
                    const element = array[index];
                    element.isCheck = false
                    for (let index = 0; index < element.list.length; index++) {
                        const ele = element.list[index];
                        ele.isCheck = false
                    }
                }
                this.setState({
                    listCategory: array,

                })

            } else {
                message.warning(res.data.errorMessage)
            }
        })
        this.getShieldList().then(res => {
            if (res.data.success) {
                this.setState({
                    shieldList: res.data.list
                })
            }
        })
    }
    onEditorStateChange = (editorState) => {
        this.setState({
            editorState,
        });
    };
    onContentStateChange = (contentState) => {
        let arr = []
        this.state.shieldList.map(res => {
            // console.log(draftToHtml(convertToRaw(this.state.editorState.getCurrentContent())).indexOf(res.searchKey))
            if (draftToHtml(convertToRaw(this.state.editorState.getCurrentContent())).indexOf(res.searchKey) != -1) {
                arr.push(res.searchKey)
            }
        })
        if (arr.length > 0) {
            message.warning(`${arr.join(',')}是关键字，请删除内容中的关键词`)
        } else {
            this.setState({
                contentState,
            });
            if (contentState.entityMap && contentState.entityMap[0]) {
                let arr = this.state.imgUrl
                arr.push(contentState.entityMap[0].data.src)
                this.setState({
                    imgUrl: arr
                })
                
            }
        }

    };
    getShieldList = async (pageNo = 1, pageSize = 1000000) => {
        let res = api('getShieldList', 'post', { pageNo, pageSize })
        return res
    }
    uploadImageCallBack = async (file) => {
        console.log(file)

        // let res=api('uploadImg','post',{formData},{
        //     'resType':1
        // })
        // if(!res.data.errorMessage){
        //     console.log(res)
        // }
        return new Promise(
            (resolve, reject) => {
                const formData = new FormData();
                formData.append('file', file);

                api('uploadImg', 'post', formData, { 'resType': 1 }).then(res => {
                    console.log(res)
                    if (res.data.success) {
                        resolve({
                            data: {
                                link: res.data.ossAddress
                            }
                        });
                    } else {
                        reject(res.data.errorMessage)
                    }
                })
            }
        );

    }
    componentDidMount() {
        NProgress.done()
    }
    componentWillUnmount() {
        NProgress.start()
    }
    handleSubmit = () => {
        let value = this.props.form.getFieldsValue(['name'])
        console.log(draftToHtml(convertToRaw(this.state.editorState.getCurrentContent())))

        if (value.name) {
            api('checkKeyword', 'post', {}, {
                'content': encodeURI(value.name+draftToHtml(convertToRaw(this.state.editorState.getCurrentContent()))),

            }).then(res => {
                if (res.data.data.length==0) {
                    console.log(res)
                    if (this.state.contentState && this.state.contentState.blocks[0].text != "") {
                        if (this.props.location.params && this.props.location.params.id) {
                            api('editArticle', 'post', {}, {
                                'articleId': this.props.location.params.id,
                                'adminId': this.state.backUserId,
                                'title': value.name,
                                'content': this.state.editorState ? encodeURI(draftToHtml(convertToRaw(this.state.editorState.getCurrentContent()))) : '',
                                "firstImg": this.state.imgUrl[0],
                                'secCategoryId': this.state.secondId != 0 ? this.state.secondId : '',
                                'categoryId': this.state.secondId != 0 ? this.state.secondId : this.state.firstId,
                                'firstCategoryId': this.state.firstId,
                            }).then(res => {
                                if (!res.data.errorMessage) {
                                    console.log(res)
                                    message.success('修改成功！')
                                    // this.props.history.go(-1)
                                    this.props.history.push('/Article/Index')

                                } else {
                                    message.warning(res.data.errorMessage, 2, () => {
                                        this.props.history.push('/Article/Index')
                                        // this.props.history.go(-1)
                                    })
                                }
                            })
                        } else {
                            if (this.state.firstId != 0 || this.state.secondId != 0) {
                                console.log(this.state.imgUrl[0])
                                api('addArticle', 'post', {
                                    "backUserId": this.state.backUserId,
                                    "content": this.state.editorState ? draftToHtml(convertToRaw(this.state.editorState.getCurrentContent())) : '',
                                    "firstImg": this.state.imgUrl[0],
                                    "status": "1",
                                    "title": value.name,
                                    "type": 1,
                                    "videoLength": '',
                                    "video_url": "",
                                    'deptId': this.state.deptId,
                                    'secCategoryId': this.state.secondId != 0 ? this.state.secondId : '',
                                    'categoryId': this.state.secondId != 0 ? this.state.secondId : this.state.firstId,
                                    'firstCategoryId': this.state.firstId,

                                }).then(res => {
                                    if (!res.data.errorMessage) {
                                        console.log(res)
                                    message.success('添加成功，等待审核中...')

                                        this.props.history.push('/Article/Index')
                                    } else {
                                        message.warning(res.data.errorMessage, 2, () => {
                                            this.props.history.push('/Article/Index')
                                            // this.props.history.go(-1)
                                        })
                                    }
                                })
                            } else {
                                message.warning('请选择类目！')

                            }
                        }

                    } else {
                        message.warning('请输入文章内容！')
                    }
                } else {
                    // message.warning(res.data.errorMessage)
                    message.warning(`${res.data.data.join(',')}是关键字，请删除内容中的关键词`)
                }
            })

        } else {
            message.warning('请输入文章标题!')
        }

    }
    setCancel = () => {
        this.props.history.go(-1)
    }
    onChange = (e) => {
        let arr = []
        this.state.shieldList.map(res => {
            console.log(e.target.value.indexOf(res.searchKey))
            if (e.target.value.indexOf(res.searchKey) != -1) {
                arr.push(res.searchKey)
            }
        })
        if (arr.length > 0) {
            message.warning(`${arr.join(',')}是关键字，请删除内容中的关键词`)
        }
        if (e.target.value.length > 60) {
            this.props.form.setFieldsValue({
                name: this.state.value,
            });
            return
        } else {
            this.setState({
                value: e.target.value
            })
        }

    }
    expandIcon = () => {
        return (
            <span>
                <Icon type="caret-right" />
                <Icon type="caret-left" />
            </span>
        )
    }
    onCollapseChange = (key) => {
        console.log(key)
        this.setState({
            selectId:key
        })

    }
    onChangeSecond = (val, val1) => {
        console.log(val, val1)
        let array = this.state.listCategory;
        for (let index = 0; index < array.length; index++) {
            const element = array[index];
            if (val == index) {

            }
            for (let i = 0; i < element.list.length; i++) {
                const ele = element.list[i];

                if (val == index && val1 == i) {
                    ele.isCheck = ele.isCheck ? false : true
                    element.isCheck = ele.isCheck ? false : true

                    this.setState({
                        secondId: ele.isCheck ? ele.id : 0,
                        firstId: ele.isCheck ? element.id : 0
                    })
                } else {
                    ele.isCheck = false
                }
            }
        }

        this.setState({
            listCategory: array,

        })
    }
    onChangeFirst = (val, e) => {
        console.log(val, e)
        e.preventDefault()
        let array = this.state.listCategory;
        for (let index = 0; index < array.length; index++) {
            const element = array[index];
            if (val == index) {
                element.isCheck = element.isCheck ? false : true
                this.setState({
                    firstId: element.isCheck ? element.id : 0
                })
            } else {
                element.isCheck = false
            }
            for (let i = 0; i < element.list.length; i++) {
                const ele = element.list[i];

                ele.isCheck = false
                this.setState({
                    secondId: 0
                })
            }
        }
        this.setState({
            listCategory: array,
            firstId:this.state.listCategory[val].id,
            selectId:this.state.listCategory[val].id
        })
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        const { editorState, contentState } = this.state;
        return (
            <div id="edit">

                <Card bordered={false} className='card-item'>
                    <Editor
                        editorState={editorState}
                        onEditorStateChange={this.onEditorStateChange}
                        onContentStateChange={this.onContentStateChange}
                        wrapperClassName="wrapper-class"
                        editorClassName="editor-class"
                        toolbarClassName="toolbar-class"
                        localization={{ locale: 'zh' }}
                        toolbar={{
                            image: { uploadCallback: this.uploadImageCallBack,alignmentEnabled:true, alt: { present: false, mandatory: false },},
                            
                        }}
                    />
                    <div className="title">标题</div>
                    <div className="textArea">
                        <Form >
                            <Form.Item label=''>
                                {getFieldDecorator('name', {
                                    initialValue: this.state.value
                                })(<Input
                                    placeholder="请输入文章标题"
                                    autocomplete="off"
                                    onChange={this.onChange}
                                    maxLength='50'
                                />)}
                            </Form.Item>

                        </Form>
                        <div className="textLength">{this.state.value.length}/50</div>
                    </div>
                    <div className="title">选择类目</div>
                    <div className="columnList">
                        <Collapse defaultActiveKey={this.state.selectId ? this.state.selectId.toString() : '0'} activeKey={[this.state.selectId ? this.state.selectId.toString() : '0']} accordion={true} expandIcon={expandicon} bordered={false} showArrow={false} onChange={this.onCollapseChange}>
                            {
                                this.state.listCategory.map((res, index) => {
                                    if (res.list.length > 0) {
                                        return (
                                            <Panel header={<span className='first' key={res.id}>{res.name}</span>} key={res.id} >
                                                <div >
                                                    {res.list && res.list.map((res, i) => {
                                                        if (res && res.name) {

                                                            return (<span className='second' onClick={() => {
                                                                this.onChangeSecond(index, i)
                                                            }}>{
                                                                    res.id==this.state.secondId ? <Icon type="check-circle" theme="filled" /> : <span className='icon'></span>
                                                                }{res.name}</span>)
                                                        }


                                                    })}
                                                </div>
                                            </Panel>
                                        )
                                    } else {
                                        if(res.id!=1){
                                            return (<div className='firstColumn'>
                                            <span className='second' onClick={(e) => {
                                                this.onChangeFirst(index, e)
                                            }}>{
                                                    res.id==this.state.firstId ? <Icon type="check-circle" theme="filled" /> : <span className='icon'></span>
                                                }{res.name}</span>
                                        </div>)
                                        }
                                       
                                    }

                                })
                            }

                        </Collapse>
                    </div>
                    <div className="btn">
                        <span className='submit' onClick={this.handleSubmit}>发布</span>
                        <span className='cancel' onClick={this.setCancel}>取消</span>
                    </div>
                </Card>

                <BackTop visibilityHeight={200} style={{ right: 50 }} />
            </div>
        )
    }
}
const WrappedRegistrationForm = Form.create()(Index);
export default WrappedRegistrationForm