import React from "react";
import { inject, observer } from 'mobx-react/index'

import {Route,Switch} from "react-router-dom";
import Container from "./Container";
import Edit from "../Edit/Index";

import './index.css'


@inject('articleStore','headerStore') @observer

class Index extends React.Component {

    constructor(props) {
        super(props)
        this.props.headerStore.setCurrunt('/Article/Index')
        console.log(this.props)

    }
    render() {

        return (
            <div id="article">
                <Switch>
                    <Route  path='/Article/Index' component={Container}></Route>
                    <Route exact path='/Article/Edit' component={Edit}></Route>
                </Switch>
            </div>
        )
    }
}

export default Index