import React from 'react'
import { BrowserRouter as Router, Route, NavLink } from 'react-router-dom';
import api from "../../../api/api";
import { Input, Tabs, Menu, Icon } from 'antd';
import './index.css'

const TabPane = Tabs.TabPane;
const SubMenu = Menu.SubMenu;
const Search = Input.Search;


class Slider extends React.Component {
    constructor(props) {
        super(props)

    }

    render() {
        return (
            <div id='Slider'>
                <div className="search" >
                    <Search
                        placeholder="搜索"
                        onSearch={this.getSearch}
                        style={{ width: '91.95%' }}
                    />
                </div>
                <div className="listLink">
                    <NavLink to='/Article/Index' exact activeClassName='link-active'>
                        <p>全部文章</p>
                    </NavLink>
                    <NavLink to='/Article/Index/ExamineDept'>
                        <p>待审核</p>
                        <NavLink to='/Article/Index/ExamineDept' activeClassName='link-active'>
                            <p>部门审核</p>
                        </NavLink>
                        <NavLink to='/Article/Index/ExamineColumn' activeClassName='link-active'>
                            <p>栏目审核</p>
                        </NavLink>
                    </NavLink>
                    <NavLink to='/Article/Index/HomePage' activeClassName='link-active'>
                        <p>首页管理</p>
                    </NavLink>
                    <NavLink to='/Article/Index/Column' activeClassName='link-active'>
                        <p>栏目管理</p>
                    </NavLink>
                    <NavLink to='/Article/Index/Banner' activeClassName='link-active'>
                        <p>轮播管理</p>
                    </NavLink>
                </div>
            </div>
        )
    }
}
const Styles = {
    submenu: {
        'paddingLeft': '16px'
    }
}
export default Slider